export default {
	data() {
		return {
			address:"",
			height:"",
			background:{
				backgroundImage:"linear-gradient(90deg, #00ABEB, #54C3F1)",
			},
			type:"个人",
			bill:"增值税电子普通发票",
			list: [{
					text: '增值税电子普通发票',
					color: '#F86032',
					fontSize: 28
				},
				{
					text: '增值税电子专用发票'
				},
				{
					text: '增值税纸质普通发票'
				},
				{
					text: '增值税纸质普通发票'
				},
			],
			tips: {
				text: '发票类型选择',
				color: '#000000',
				fontSize: 36
			},
			show: false,
			form: {
				name: '',
				intro: '',
			},
			radio: '个人', //默认选中
			radioList: [{
					name: '个人',
					disabled: false,
				},
				{
					name: '企业',
					disabled: false,

				},
			],
			rules: {
				name: [{
					required: true,
					message: '请填写需要开票的个人姓名',
					// 可以单个或者同时写两个触发验证方式 
					trigger: ['change', 'blur'],
				}],
				address: [{
					min: 5,
					message: '请填写需要开票的个人地址',
					trigger: 'change'
				}],
				tel: [{
					min: 11,
					max: 11,
					message: '请填写需要开票的11位手机号码',
					trigger: 'change'
				}],

			}
		};
	},
	methods: {
		submit() {
			this.$refs.uForm.validate(valid => {
				if (valid) {
					console.log('验证通过');
				} else {
					console.log('验证失败');
				}
			});
		},
		// 选中某个单选框时，由radio时触发
		radioChange(e) {
			console.log(e);
			this.type  =e;
		},
		// opensheet
		opensheet() {
			this.show = true;
		},
		//选择发票类型
		click(index) {
			console.log(`点击了第${index + 1}项，内容为：${this.list[index].text}`);
			this.bill =`${this.list[index].text}`;
		}
	},
	// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
	onReady() {
		this.$refs.uForm.setRules(this.rules);
	}
};
